import React from 'react';
import {
  CDBSidebar,
  CDBSidebarContent,
  CDBSidebarFooter,
  CDBSidebarHeader,
  CDBSidebarMenu,
  CDBSidebarMenuItem,
} from 'cdbreact';

const Sidebar = () => {

  const session = JSON.parse(window.sessionStorage.getItem("data"));

  const MyPage = () => {
    window.location.replace("/Mypage")
  }
  const Cart = () => {
    window.location.replace("/Cart")
  }
  const Shopping_info = () => {
    window.location.replace("/Shopping_info")
  }
  const AddStore = () => {
    window.location.replace("/AddStore")
  }
  const EditStore = () => {
    window.location.replace("/EditStore")
  }
  const AddItem = () => {
    window.location.replace("/AddItem")
  }
  const EditItem = () => {
    window.location.replace("/EditItem")
  }
  const Check_Info= () => {
    window.location.replace("/Check_Info")
  }
  const Call = () => {
    window.open("/Chat", "", "width=650, height=800, toolbar=no, menubar=no, scrollbars=no, resizable=yes");
    window.location.replace("/Town")
  }
  

  const logout = () => {
    if (window.confirm(" ?") == true) {    //
      window.sessionStorage.clear();
      window.location.replace("/")
      alert(' .');
    } else {   //
      if (window.location.replace("/Town") === true
      ) {
        window.location.replace("/Gather")
      }
      else {
        window.location.replace("/Town")
      }
    }
  }

  return (
    <div
      style={{ display: 'flex', height: '95vh', overflow: 'scroll initial' }}
    >
      <CDBSidebar textColor="#fff" backgroundColor="#333">
        <CDBSidebarHeader prefix={<i className="fa fa-bars fa-large"></i>}>
          <a
            className="text-decoration-none"
            style={{ color: 'inherit' }}
          >
            {session.data.name + ' '}
          </a>
        </CDBSidebarHeader>
        <CDBSidebarContent className="sidebar-content">
          <CDBSidebarMenu>
            <CDBSidebarMenuItem onClick={MyPage} icon="address-card"> </CDBSidebarMenuItem>
            <CDBSidebarMenuItem onClick={Cart} icon="heart"></CDBSidebarMenuItem>
            <CDBSidebarMenuItem onClick={Shopping_info} icon="money-check-alt"> </CDBSidebarMenuItem>
            <div>
              {
                //   if
                session.data.type === 'SELLER'
                  ? <CDBSidebarMenuItem onClick={EditStore} icon="home">  </CDBSidebarMenuItem>
                  : <CDBSidebarMenuItem onClick={AddStore} icon="home">  </CDBSidebarMenuItem>
              }
            </div>
            {
              session.data.type === 'SELLER' ? 
                <CDBSidebarMenuItem onClick={AddItem} icon="archive">  </CDBSidebarMenuItem>
                : <></>
            }
            {
              session.data.type === 'SELLER' ? 
                <CDBSidebarMenuItem onClick={EditItem} icon="archive">  </CDBSidebarMenuItem>
                : <></>
            }
                        {
              session.data.type === 'SELLER' ? 
                <CDBSidebarMenuItem onClick={Check_Info} icon="archive">   </CDBSidebarMenuItem>
                : <></>
            }
            {
              session.data.type === 'SELLER' ? 
                <CDBSidebarMenuItem onClick={Call} icon="phone"> </CDBSidebarMenuItem>
                : <></>
            }

          </CDBSidebarMenu>
        </CDBSidebarContent>
        <CDBSidebarMenuItem onClick={logout} style={{ color: 'white' }} icon="window-close" ></CDBSidebarMenuItem>
        <CDBSidebarFooter style={{ textAlign: 'center' }} >
          <div
            style={{
              padding: '40px 10px',
            }}
          >
             Team : 
          </div>
        </CDBSidebarFooter>
      </CDBSidebar>
    </div>
  );
};

export default Sidebar;