import { Form } from 'react-final-form';
import Box from '@mui/material/Box';
import Link from '@mui/material/Link';
import Typography from '../modules/components/Typography';
import AppFooter from '../modules/views/AppFooter';
import AppAppBar from '../modules/views/AppBar';
import AppForm from '../modules/views/AppForm';
import FormButton from '../modules/form/FormButton';
import withRoot from '../modules/withRoot';
import React, { useState } from 'react';
import axios from 'axios'

function Login() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const onEmailHandler = (event) => {
    setEmail(event.currentTarget.value);
  }

  const onPasswordHandler = (event) => {
    setPassword(event.currentTarget.value)
  }


  const onClickLogin = () => {
    console.log('click login')
    console.log('ID : ', email)
    console.log('PW : ', password)
    let data = JSON.stringify({
      'password': password,
      'email': email
    })
    axios.post('http://localhost:8080/login', data, {
      headers: {
        'Content-type': 'application/json; charset=utf-8',
      }
    })

      .then(res => {
        const session = res.data;
        const userObj = { data: session };
        window.sessionStorage.setItem("data", JSON.stringify(userObj));
        // 
        // window.sessionStorage.removeItem(key)  .
        console.log(' ' + res.data.name)
        if (res.data.email === undefined) {
          // id    userId = undefined, msg = ' id   .'
          alert('    .')
        } else if (res.data.email === null) {
          // id , pw    userId = null , msg = undefined
          console.log('======================', '    .')
          alert('    .')
        } else if (res.data.email === email && res.data.email !== 'admin') {
          // id, pw   userId = userId1, msg = undefined
          console.log('======================', ' ')
          alert(res.data.name + ' .')
          //sessionStorage.setItem('email', email)
          document.location.href = '/Town'
        }
        else if (res.data.email === 'admin') {
          //  
          alert(' .')
          document.location.href = '/admin'
        }
      })
      .catch()
  }

  return (
    <React.Fragment>
      <AppAppBar />
      <AppForm>
        <React.Fragment>
          <Typography
            color="inherit"
            variant="h3"
            align="center">
            Onnuri
          </Typography>
        </React.Fragment>
        <Form
          onSubmit={onClickLogin}
        >
          {({ handleSubmit: handleSubmit2, submitting }) => (
            <Box component="form" onSubmit={handleSubmit2} noValidate sx={{ mt: 6 }}>
              <Typography variant="h8" >
                
              </Typography>
              <br />
              <input type="text"
                label=""
                name="email"
                style={{
                  fontSize: 25,
                  padding: 20,
                  width: 530,
                  height: 80,
                  border: "2px solid black",
                  collapse: 'collapse',
                  borderRadius: '8px',
                }}
                value={email}
                onChange={onEmailHandler}
              />
              <br />
              <br />
              <Typography variant="h8" >
                
              </Typography>
              <br />
              <input type="password"
                name="password"
                style={{
                  fontSize: 25,
                  width: 530,
                  padding: 20,
                  height: 80,
                  border: "2px solid black",
                  collapse: 'collapse',
                  borderRadius: '8px',
                }}
                value={password}
                onChange={onPasswordHandler}
              />
              <br />
              <br />
              {/* <input name="email" type="email" placeholder="" value={email} onChange={onEmailHandler} />
            <input name="password" type="password" placeholder="" value={password} onChange={onPasswordHandler} />
            <button type="submit" onSubmit={onClickLogin}></button> */}
              <Typography variant="body2" align="center">
                {' ?    . '}
                <Link
                  href="/Signup"
                  align="center"
                  underline="always"
                >
                   
                </Link>
              </Typography>
              <FormButton
                sx={{ mt: 1, mb: 1 }}
                size="large"
                color="primary"
                fullWidth
                style={{
                  padding: 8,
                  border: "4px solid black",
                  collapse: 'collapse',
                  borderRadius: '8px',
                }}
                type="submit"
                onSubmit={onClickLogin}
              >
                {''}
              </FormButton>
              <FormButton
                sx={{ mt: 1, mb: 1 }}
                //disabled={submitting || sent}
                size="large"
                fullWidth
                style={{
                  padding: 8,
                  color: "blue",
                  backgroundColor: "white",
                  border: "4px solid blue",
                  collapse: 'collapse',
                  borderRadius: '8px',
                }}
              >
                <img
                  src="/assets/google.png"
                />
                {' '}
              </FormButton>
              <FormButton
                sx={{ mt: 1, mb: 1 }}
                //disabled={submitting || sent}
                size="large"
                fullWidth
                style={{
                  padding: 8,
                  color: "black",
                  backgroundColor: "gold",
                  border: "4px solid gold",
                  collapse: 'collapse',
                  borderRadius: '8px',
                }}
              >
                <img
                  src="/assets/kakao.png"
                />
                {' '}
              </FormButton>
              <FormButton
                sx={{ mt: 1, mb: 1 }}
                //disabled={submitting || sent}
                size="large"
                fullWidth
                style={{
                  padding: 8,
                  backgroundColor: "limegreen",
                  border: "4px solid limegreen",
                  collapse: 'collapse',
                  borderRadius: '8px',
                }}
              >
                <img
                  src="/assets/naver.png"
                />
                {' '}
              </FormButton>
            </Box>
          )}
        </Form>
      </AppForm>
      <AppFooter />
    </React.Fragment>
  );
}

export default withRoot(Login);
